import {
  Component,
  OnDestroy,
  ViewChild,
  AfterViewInit
} from '@angular/core';
import * as _ from 'lodash';
import { Appointment } from 'src/app/shared/models/appointment';
import { Offer } from 'src/app/shared/models/offer';
import { NavbarButtonsService } from 'src/app/core/services/navbar-buttons-service/navbar-buttons.service';
import { AppointmentService } from 'src/app/core/services/appointment-service/appointment.service';
import { OfferService } from 'src/app/core/services/offer-service/offer.service';
import { AppointmentTypes } from 'src/app/shared/enums/static-strings';
import { ActivatedRoute } from '@angular/router';
import { Subscription } from 'rxjs/internal/Subscription';
import { TabsetComponent } from 'ng-uikit-pro-standard';

@Component({selector: 'app-marketplace-wrapper',
templateUrl: './marketplace-wrapper.component.html',
styleUrls: ['./marketplace-wrapper.component.scss']})
export class MarketplaceWrapperComponent implements AfterViewInit,
OnDestroy {
  appointments: Array < Appointment >;
  @ViewChild('MarketPlaceTabs') public marketPlaceTabs: TabsetComponent;
  sentOffers: Array < Offer >;
  receivedOffers: Array < Offer >;
  partedAppointments: Array < Appointment[] > = [];
  filteredAppointments: Array < Appointment > = [];
  receivedOfferCounter = 0;
  sentOfferCounter = 0;
  tabIndex = 1;
  _sentOfferSubscription: Subscription;
  _receivedOfferSubscription: Subscription;
  _allAppointmentsSubscription: Subscription;
  _routeSubscription: Subscription;
  constructor(
    private navBarButtons: NavbarButtonsService,
    private appointmentService: AppointmentService,
    private offerService: OfferService,
    private route: ActivatedRoute) {
    this.navBarButtons.changeToMarketPlaceView();
    this._allAppointmentsSubscription = this
      .appointmentService
      ._allAppointments
      .subscribe(appointments => {
        this.appointments = appointments;
        this.fetchAppointments();
      });
    this._sentOfferSubscription = this
      .offerService
      ._sentOffers
      .subscribe(sentOffers => {
        this.sentOfferCounter = 0;
        this.sentOffers = sentOffers;
        console.log(this.sentOffers);
        this
          .sentOffers
          .forEach(sentOffer => {
            if (sentOffer.offerStatus === 'Pending') {
              this.sentOfferCounter++;
            }
          });
      });
    this._receivedOfferSubscription = this
      .offerService
      ._receivedOffers
      .subscribe(receivedOffers => {
        this.receivedOfferCounter = 0;
        this.receivedOffers = receivedOffers;
        console.log(this.receivedOffers);
        this
          .receivedOffers
          .forEach(receivedOffer => {
            if (receivedOffer.offerStatus === 'Pending') {
              this.receivedOfferCounter++;
            }
          });
      });
  }
  ngAfterViewInit(): void {
    this._routeSubscription = this.route.queryParams.subscribe(queryParams => {
      if (!!queryParams) {
        const index: number = +queryParams['tabIndex'];
        this.tabIndex = index;
        this.marketPlaceTabs.setActiveTab(index);
      }
    });
  }
  changeViewRight() {
    if (this.tabIndex === 1) {
      this.tabIndex = 3;
      this.marketPlaceTabs.setActiveTab(this.tabIndex);
    } else {
      this.tabIndex--;
      this.marketPlaceTabs.setActiveTab(this.tabIndex);
    }
  }
  changeViewLeft() {
    if (this.tabIndex === 3) {
      this.tabIndex = 1;
      this.marketPlaceTabs.setActiveTab(this.tabIndex);
    } else {
      this.tabIndex++;
      this.marketPlaceTabs.setActiveTab(this.tabIndex);
    }
  }
  fetchAppointments() {
    this.filteredAppointments = [];
    this
      .appointments
      .forEach(appointment => {
        if (appointment.type === AppointmentTypes[AppointmentTypes.FreierDienst]
          || appointment.type === AppointmentTypes[AppointmentTypes.MarktplatzDienst]) {
          this
            .filteredAppointments
            .push(appointment);
        }
      });
    this.partedAppointments = [];
    this.partedAppointments = Appointment.groupAppointments(this.filteredAppointments);
  }
  tabActivated(e) {
    console.log(e);
    if (e != null) {
      if (e.activeTabIndex === 1) {
        this.tabIndex = 2;
        this.navBarButtons.isOfferView = true;
      } else if (e.activeTabIndex === 2) {
        this.tabIndex = 3;
        this.navBarButtons.isOfferView = true;
      } else {
        this.tabIndex = 1;
        this.navBarButtons.isOfferView = false;
      }
    }
  }
  ngOnDestroy() {
    this.navBarButtons.isOfferView = false;
    this._allAppointmentsSubscription.unsubscribe();
    this._receivedOfferSubscription.unsubscribe();
    this._sentOfferSubscription.unsubscribe();
  }
}
